import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { Feature } from 'shared-code-lib';
import {
  Account,
  AccountRegistrationDetails,
  IPurchaseSummary,
  InboundService,
  OutboundService,
} from '../../../blocks/InterFAX/Models/admin';
import { WizardBarPage } from '../../../blocks/components/wizard-bar/wizard-bar.component';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResults, Control } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { ISummaryProvider } from '../account-components/parts-invoice/parts-invoice.component';

class InboundSummaryProvider implements ISummaryProvider {
  constructor(private interfaxService: InterFAXService, private userid: string, private inbound: InboundService) {}

  load(successCallBack: (ip: IPurchaseSummary) => void, failureCallBack: (err: any) => void): void {
    this.interfaxService.getInboundPurchaseSummary(this.userid, this.inbound, successCallBack, failureCallBack);
  }
}

class OutboundSummaryProvider implements ISummaryProvider {
  constructor(private interfaxService: InterFAXService, private userid: string, private outbound: OutboundService) {}

  load(successCallBack: (ip: IPurchaseSummary) => void, failureCallBack: (err: any) => void): void {
    this.interfaxService.getOutboundPurchaseSummary(this.userid, this.outbound, successCallBack, failureCallBack);
  }
}

@Component({
  selector: 'app-fax-user-add-service',
  templateUrl: './fax-user-add-service.component.html',
  styleUrls: ['./fax-user-add-service.component.scss'],
})
export class FaxUserAddServiceComponent implements OnInit, OnDestroy {
  @ViewChild('spinnerWrapper', { static: true })
  spinnerWrapper: ElementRef;

  formControls: Array<Control> = [];

  account: Account;
  accountRegistrationDetails: AccountRegistrationDetails;

  outbound = new OutboundService();
  inbound = new InboundService();

  pages = new Array<WizardBarPage>();
  selectedPage = 0;

  selectedPageId = '';
  nextButtonText: string;

  show = false;

  valid = false;

  routeParams: Params;
  routeData: Data;

  summaryProvider: ISummaryProvider;

  errors: Array<any>;
  skipConfirmation = false;

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private spinnerService: SpinnerStateService,
    private interfaxService: InterFAXService,
    private toastrService: ToastrService,
    private router: Router,
    private errorsService: ErrorsService,
    private route: ActivatedRoute,
    private userStateService: UserStateService
  ) {}

  ngOnInit(): void {
    this.outbound.enabled = true;
    this.inbound.enabled = true;

    this.skipConfirmation = this.userStateService.hasFeature(Feature.InterFAX_SkipAddingServiceConfirmation);

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        this.load();
      })
    );
  }

  onPageValid = (valid: boolean) => {
    this.valid = valid;
  };

  addUserService = (): void => {
    this.spinnerService.show(
      this.translateService.instant('fax-user-service.adding-service'),
      this.spinnerWrapper.nativeElement
    );

    if (this.routeData.type == 'inbound') {
      this.interfaxService.addInboundService(
        this.routeParams.userid,
        this.inbound,
        this.addServiceSucceeded,
        this.addServiceFailed
      );
    } else {
      this.interfaxService.addOutboundService(
        this.routeParams.userid,
        this.outbound,
        this.addServiceSucceeded,
        this.addServiceFailed
      );
    }
  };

  addServiceSucceeded = (r: ApiResults): void => {
    this.spinnerService.hide();
    this.toastrService.success(this.translateService.instant('fax-user-service.add-service-succeeded'));
    this.close();
  };

  addServiceFailed = (e: any): void => {
    this.spinnerService.hide();
    this.errorsService.showErrorsModal(e, this.translateService.instant('fax-user-service.add-service-failed'));
  };

  private load = () => {
    this.spinnerService.show(this.translateService.instant('Common.LoadingData'), this.spinnerWrapper.nativeElement);

    this.interfaxService.getAccountRegistrationDefaults('', this.accountRegistrationDefaultsLoaded, this.loadFailed);
  };

  accountRegistrationDefaultsLoaded = (ard: AccountRegistrationDetails): void => {
    this.accountRegistrationDetails = ard;

    this.interfaxService.getAccount(this.accountLoaded, this.loadFailed);
  };

  accountLoaded = (a: Account): void => {
    this.account = a;

    this.loadPages();
    this.update();

    this.spinnerService.hide();
    this.show = true;
  };

  createPurchaseSummary = (): void => {
    if (this.routeData.type == 'inbound') {
      this.summaryProvider = new InboundSummaryProvider(this.interfaxService, this.routeParams.userid, this.inbound);
    } else {
      this.summaryProvider = new OutboundSummaryProvider(this.interfaxService, this.routeParams.userid, this.outbound);
    }
  };

  loadPages = (): void => {
    this.addPage(`service-${this.routeData.type}`);
    this.addPage('confirmation');
  };

  loadFailed = (): void => {
    //todo:
    this.spinnerService.hide();
  };

  onCancel = () => {
    //tbd: CONFIRM?

    this.close();
  };

  onBack = () => {
    this.valid = false;
    this.selectedPage--;
    this.update();
  };

  onNext = () => {
    this.errors = null;

    if (this.selectedPage == this.pages.length - 1) {
      this.addUserService();
    } else {
      this.createPurchaseSummary();

      this.spinnerService.show(
        this.translateService.instant('fax-user-service.validating'),
        this.spinnerWrapper.nativeElement
      );

      this.summaryProvider.load(this.summaryValidated, this.summaryFailedValidation);
    }
  };

  summaryValidated = (ps: IPurchaseSummary): void => {
    if (!this.skipConfirmation) this.spinnerService.hide();

    this.selectedPage++;
    this.valid = false;
    this.update();
  };

  summaryFailedValidation = (e): void => {
    this.spinnerService.hide();
    this.errors = [e];
  };

  close = () => {
    this.router.navigateByUrl(
      '/product/settings/InterFAX/InterFAX-Upland.Bamba.InterFAX.Administration.FaxUsers.Folder'
    );
  };

  update = (): void => {
    this.nextButtonText = this.getNextButtonText();
    this.selectedPageId = this.pages[this.selectedPage].id;
  };

  addPage = (id: string): void => {
    this.pages.push(new WizardBarPage(id, this.translateService.instant(`fax-user-service.wizard-page-${id}`)));
  };

  getTitle = (): string => {
    const prefix = this.translateService.instant(`fax-user-service.header-text-${this.routeData.type}`);

    return `${prefix} ${this.routeParams.userid}`;
  };

  getNextButtonText(): string {
    if (this.selectedPage === this.pages.length - 1 || this.skipConfirmation) return 'fax-user-service.add-service';
    return 'App.next';
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
