import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import {
  AutoFocusFirstInputModule,
  TabBarModule,
  FieldsModule,
  CheckboxModule,
  ModalHeaderModule,
  LabelModule,
  IconsModule,
} from 'shared-code-lib';
import { PageModule } from '../../../blocks/page/page.module';
import { TranslateModule } from '@ngx-translate/core';
import { ComponentsModule } from '../../../blocks/components/components.module';
import { FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalModule } from 'ngx-smart-modal';
import { RouterModule, Routes } from '@angular/router';
import { FaxUserAddServiceComponent } from './fax-user-add-service.component';
import { AccountComponentsModule } from '../account-components/account-components.module';
import { AddServiceConfirmationComponent } from './add-service-confirmation/add-service-confirmation.component';

const routes: Routes = [
  {
    path: 'inbound/:userid',
    component: FaxUserAddServiceComponent,
    data: { type: 'inbound' },
    pathMatch: 'full',
  },

  {
    path: 'outbound/:userid',
    component: FaxUserAddServiceComponent,
    data: { type: 'outbound' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  declarations: [],
})
export class FaxUserAddServiceComponentRoutingModule {}

@NgModule({
  declarations: [FaxUserAddServiceComponent, AddServiceConfirmationComponent],
  imports: [
    CommonModule,
    TranslateModule,
    PageModule,
    ComponentsModule,
    FormsModule,
    PerfectScrollbarModule,
    IconsModule,
    FieldsModule,
    AutoFocusFirstInputModule,
    TabBarModule,
    CheckboxModule,
    NgxSmartModalModule,
    ModalHeaderModule,
    LabelModule,
    FaxUserAddServiceComponentRoutingModule,
    AccountComponentsModule,
  ],
})
export class FaxUserAddServiceComponentModule {}
