import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { InboundService, OutboundService } from '../../../../blocks/InterFAX/Models/admin';
import { ISummaryProvider } from '../../account-components/parts-invoice/parts-invoice.component';

@Component({
  selector: 'app-add-service-confirmation',
  templateUrl: './add-service-confirmation.component.html',
  styleUrls: ['./add-service-confirmation.component.scss'],
})
export class AddServiceConfirmationComponent implements OnInit {
  @Input() inbound: InboundService;
  @Input() outbound: OutboundService;
  @Input() summaryProvider: ISummaryProvider;
  @Input() shouldSkipConfirmation: boolean;

  @Output() valid = new EventEmitter<boolean>();
  @Output() skipConfirmation = new EventEmitter<void>();

  ngOnInit(): void {
    this.valid.next(false);
  }

  onDisplay(): void {
    this.valid.next(true);
    if (!this.shouldSkipConfirmation) return;
    this.skipConfirmation.emit();
  }
}
